import {useState} from 'react'

export default function Register() {

    const [username,setUsername] = useState('');
    const [name,setName] = useState('');
    const [email,setEmail] = useState('');
    const [password,setPassword] = useState('');
    const [confirmPassword,setConfirmPassword] = useState('');
    
    const submitForm = async(e) =>{
        e.preventDefault();
        if(password !== confirmPassword){
            alert('Password doesn\'t match');
            return;
        }
        const obj = {username,name,email,password,confirmPassword};
           try{
               const url = process.env.REACT_APP_API_URL + '/register';
                const e  =  await fetch(url,{
                    method:"POST",
                    headers:{
                    "Content-Type":"application/json"
                    },
                    body:JSON.stringify(obj)
               })
                const { data } = await e.json();
                localStorage.setItem('user',data.user);

           }catch(err){
                console.log(err)
           }
    }
   
    return (
        <div className="container">
            <div className="columns is-one-third-widescreen is-centered">
                <div className="column is-half">
                    <div id="register" className="mt-6 mb-4 p-6 has-background-white box">
                        <h1 className="title my-3">Registration Form</h1>
                        <form id="register-form" onSubmit={submitForm}>
                            <div className="field my-5">
                                <p className="control has-icons-left has-icons-right">
                                    <input onKeyUp={(e)=>setUsername(e.target.value)} className="input" type="text" placeholder="Username" />
                                    <span className="icon is-small is-left">
                                        <i className="fas fa-user"></i>
                                    </span>
                                </p>
                            </div>
                            <div className="field my-5">
                                <p className="control has-icons-left has-icons-right">
                                    <input onKeyUp={(e)=>setName(e.target.value)} className="input" type="text" placeholder="Name" />
                                    <span className="icon is-small is-left">
                                        <i className="fas fa-user"></i>
                                    </span>
                                </p>
                            </div>
                            <div className="field my-5">
                                <p className="control has-icons-left has-icons-right">
                                    <input onKeyUp={(e)=>setEmail(e.target.value)} className="input" type="email" placeholder="Email" />
                                    <span className="icon is-small is-left">
                                        <i className="fas fa-envelope"></i>
                                    </span>
                                </p>
                            </div>
                            <div className="field my-5">
                                <p className="control has-icons-left">
                                    <input onKeyUp={(e)=>setPassword(e.target.value)} className="input" type="password" placeholder="Password" />
                                    <span className="icon is-small is-left">
                                        <i className="fas fa-lock"></i>
                                    </span>
                                </p>
                            </div>
                            <div className="field my-5">
                                <p className="control has-icons-left">
                                    <input onKeyUp={(e)=>setConfirmPassword(e.target.value)} className="input" type="password" placeholder="Confirm Password" />
                                    <span className="icon is-small is-left">
                                        <i className="fas fa-lock"></i>
                                    </span>
                                </p>
                            </div>
                            <div className="field my-5">
                                <p className="control">
                                    <button className="button is-success">Submit</button>
                                </p>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}
