import { useState } from 'react'

export default function AddNewProperty() {

    const [title, setTitle] = useState('');
    const [market_type, setMarket_type] = useState('');
    const [property_type, setProperty_type] = useState('');
    const [address, setAddress] = useState('');
    const [price, setPrice] = useState('');
    const [township, setTownship] = useState('');
    const [show_price, setShow_price] = useState('');
    const [phone_number, setPhone_number] = useState('');
    const [show_phone, setShow_phone] = useState('');
    const [description, setDescription] = useState('');
    const [coordinates, setCoordinates] = useState('');
    const [media, setMedia] = useState('');

    // {
    //     "title":"တောင်ကြီး ဖရဲ",	
    //     "market_type":"sale",	
    //     "property_type":"compound",	
    //     "address":"ရေမီးစုံ လမ်းသင့် ၂ ကွက်ငုတ်",
    //     "price":"170",
    //     "township":"တောင်ကြီး",
    //     "show_price":"0",
    //     "phone_number":"098348748",	
    //     "show_phone":"0",
    //     "description":"ရေမီးစုံ လမ်းသင့် ၂ က",
    //     "coordinates":"97.04078699444915,20.749849665889105",
    //     "media":"https://picsum.photos/403,https://picsum.photos/404"
    //     }

    const submitForm = async (e) => {
        e.preventDefault();

        const obj = { title, market_type, property_type, address, price, township, show_price, phone_number, show_phone, description, coordinates, media };
        try {
            const url = process.env.REACT_APP_API_URL + '/properties';
            const e = await fetch(url, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify(obj)
            })
            const { data } = await e.json();
            console.log(data)

        } catch (err) {
            console.log(err)
        }
    }

    return (
        <div className="container">
            <div className="columns is-one-third-widescreen is-centered">
                <div className="column is-half">
                    <div id="add-property" className="mt-6 mb-4 p-6 has-background-white box">
                        <h1 className="title my-3">Add New Property</h1>
                        <form id="add-property-form" onSubmit={submitForm}>
                            <div className="field my-5">
                                <p className="control has-icons-left has-icons-right">
                                    <input onKeyUp={(e) => setTitle(e.target.value)} className="input" type="text" placeholder="Title" />
                                    <span className="icon is-small is-left">
                                        <i className="fas fa-house"></i>
                                    </span>
                                </p>
                            </div>
                            <div className="field my-5">
                                <p className="control has-icons-left has-icons-right">
                                    <input onKeyUp={(e) => setMarket_type(e.target.value)} className="input" type="text" placeholder="Market Type" />
                                    <span className="icon is-small is-left">
                                        <i className="fas fa-user"></i>
                                    </span>
                                </p>
                            </div>
                            <div className="field my-5">
                                <p className="control has-icons-left has-icons-right">
                                    <input onKeyUp={(e) => setProperty_type(e.target.value)} className="input" type="email" placeholder="Property Type" />
                                    <span className="icon is-small is-left">
                                        <i className="fas fa-envelope"></i>
                                    </span>
                                </p>
                            </div>
                            <div className="field my-5">
                                <p className="control has-icons-left">
                                    <textarea onKeyUp={(e) => setAddress(e.target.value)} class="textarea" type="text" placeholder="Address" />
                                    <span className="icon is-small is-left">
                                        <i className="fas fa-lock"></i>
                                    </span>
                                </p>
                            </div>
                            <div className="field my-5">
                                <p className="control has-icons-left">
                                    <input onKeyUp={(e) => setPrice(e.target.value)} className="input" type="text" placeholder="Price" />
                                    <span className="icon is-small is-left">
                                        <i className="fas fa-lock"></i>
                                    </span>
                                </p>
                            </div>
                            <div className="field my-5">
                                <p className="control">
                                    <button className="button is-success">Add</button>
                                </p>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}
