import {useContext} from 'react'
import Spinner from './Spinner'
import Sidebar from './Sidebar'
import { AppContext } from './../ContextApi'
import { Link } from 'react-router-dom';

export default function Properties() {
    const [properties,,loading] = useContext(AppContext);
   
    const getImageUrl = (str) =>{
        let arr = str.split(",");
        arr = Array.from(arr);
        return arr[0];
    }

    return (
           <div className="columns my-4">
           {loading ? <Spinner /> : ''}
           <div className="column is-two-thirds">
           {properties && properties.map(property=>{
           return (
                    <div key={property.id} className="property box" id={property.id}>
                        <div className="columns">
                            <div className="column">
                                <div className="img-wrap">
                                    <span className="market-type">{property.market_type}</span>
                                    {property.media.length > 0 ? <img src={getImageUrl(property.media)} alt="" /> : '' }
                                </div>
                            </div>
                            <div className="column is-three-quarters">
                                <Link to={`/properties/${property.id}`}><h4 className="ah-title my-3">{property.title}</h4></Link>
                                <div className="columns">
                                    <div className="address column is-three-quarters"><span className="fas fa-map-marker mr-2"></span>{property.address}</div>
                                    <div className="price column"><span className="fas fa-credit-card mr-2"></span><strong>{property.price}</strong></div>
                                </div>
                                <div className="phone"><span className="fas fa-phone mr-2"></span>{property.phone_number}</div>
                                <div className="divider"></div>
                                <div className="columns">
                                    <div className="column is-two-thirds"></div>
                                    <div className="column has-text-right"><div className="see-detail">
                                    <Link to={`/properties/${property.id}`} className="button is-primary">See Detail</Link>
                                </div></div>
                                </div>
                                <a className="favorite"><span className="fas fa-heart"></span></a>
                            </div>
                        </div>
                    </div>
           )}
           )}
           </div>
           <div className="column">
            <Sidebar />
           </div>
           </div>
    )
}
