import { useRef, useContext, useState, useEffect } from 'react'
import { useParams } from 'react-router-dom';
import { AppContext } from './../ContextApi';
import Sidebar from './Sidebar';

import mapboxgl from '!mapbox-gl'; // eslint-disable-line import/no-webpack-loader-syntax
mapboxgl.accessToken = 'pk.eyJ1Ijoic2hhbnJlYWxlc3RhdGUiLCJhIjoiY2txcTExdHFxMTAyNDJvcGM1c2NxbWlrbCJ9.hyYX36EBjve531aIlRhAnw';

export default function SingleProperties() {
    const { id } = useParams();
    const mapContainer = useRef(null);
    const map = useRef(null);
    const [defaultCoordinate, setDefaultCoordinate] = useState([97.0275532,20.7806935]);
    const [zoom, setZoom] = useState(14);

    const [properties] = useContext(AppContext);
    const [property, setProperty] = useState({});

    const getImageUrl = (str) => {
        let arr = str.split(",");
        arr = Array.from(arr);
        return arr[0];
    }

    useEffect(() => {
        const runMapbox = (property)=>{
            const coordinate = property ? property.coordinates.split(",") : defaultCoordinate;

            if (map.current) return; // initialize map only once
                map.current = new mapboxgl.Map({
                container: mapContainer.current,
                style: 'mapbox://styles/mapbox/streets-v11',
                center: coordinate,
                zoom: zoom
            });
            map.current.addControl(new mapboxgl.FullscreenControl());
            map.current.addControl(new mapboxgl.NavigationControl());
            new mapboxgl.Marker()
            .setLngLat(coordinate)
            .setPopup(
            new mapboxgl.Popup({ offset: 25 }) // add popups
            .setHTML(`<h3>${property.title}</h3>`)
            ).addTo(map.current);
        }

        if (properties.length === 0) {
            const fetchData = async () => {
                const res    = await fetch(process.env.REACT_APP_API_URL + '/properties/' + id);
                const {data} = await res.json();
                setProperty(data);
                runMapbox(data);
            }
            fetchData();
            return;
        }

        let filtered = properties.filter(e => String(e.id) === id);
        filtered = filtered ? filtered[0] : {};
        setProperty(filtered);
        runMapbox(filtered);

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [id])

    return (
        <div id="single-property" className="columns my-4">
            <div className="column is-three-fifths">
                <div className="property box">
                    <div className="img-wrap">
                        <img src={property.media && getImageUrl(property.media)} alt={property && property.title} />
                    </div>
                    <h3 className="title is-3 my-4">{property && property.title}</h3>
                    <div className="address">{property.address && property.address}</div>
                    <div className="price">
                        {property.price && property.price}
                    </div>
                    <div className="phone">
                        {property.price && property.price}
                    </div>
                </div>
            </div>
            <div className="column">
               <div ref={mapContainer} className="map-container" />
            </div>
        </div>
    )
}
