import {useState} from 'react'

export default function Login() {

    const [email,setEmail] = useState('');
    const [password,setPassword] = useState('');
    
    const submitForm = async (e) =>{
        e.preventDefault();
        try{

            const url = process.env.REACT_APP_API_URL + '/login';
            const res = await fetch(url,{
                method:"POST",
                headers:{
                    'Content-Type' : 'application/json'
                },
                body:JSON.stringify({email,password})
            })
            const {data} = await res.json();
            if(!data) return;
            localStorage.setItem('ah_token',data.token);
            localStorage.setItem('ah_user',JSON.stringify(data.user));
            e.target.reset();
        }catch(err){
            e.target.reset();
            console.log(err)
        }
    }

    return (
        <div className="container">
            <div className="columns is-one-third-widescreen is-centered">
                <div className="column is-half">
                    <div id="login" className="mt-6 mb-4 p-6 has-background-white box">
                        <h1 className="title my-3">Login Form</h1>
                        <form onSubmit={submitForm} id="login-form">
                            <div className="field my-5">
                                <p className="control has-icons-left has-icons-right">
                                    <input onKeyUp={(e)=>setEmail(e.target.value)} className="input" type="email" placeholder="Email" />
                                    <span className="icon is-small is-left">
                                        <i className="fas fa-envelope"></i>
                                    </span>
                                </p>
                            </div>
                            <div className="field my-5">
                                <p className="control has-icons-left">
                                    <input onKeyUp={(e)=>setPassword(e.target.value)} className="input" type="password" placeholder="Password" />
                                    <span className="icon is-small is-left">
                                        <i className="fas fa-lock"></i>
                                    </span>
                                </p>
                            </div>
                            <div className="field my-5">
                                <p className="control">
                                    <button className="button is-success">Submit</button>
                                </p>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}
