import './App.css';
import Register from './components/Register';
import Login from './components/Login';
import Home from './components/Home';
import Notfound from './components/Notfound';
import {Switch,Route} from 'react-router-dom';
import Navbar from './components/Navbar'
import SingleProperties from './components/SingleProperties';
import AddNewProperty from './components/AddNewProperty';

function App() {
  return (
    <div className="app">
      <Navbar />
      <div className="container my-4">
      <Switch>
        <Route path="/" exact component={Home}/>
        <Route path="/register" exact component={Register}/>
        <Route path="/add" exact component={AddNewProperty}/>
        <Route path="/login" exact component={Login}/>
        <Route path="/properties/:id" exact component={SingleProperties}/>
        <Route>
          <Notfound />
        </Route>
      </Switch>
      </div>
    </div>
  );
}

export default App;
